import * as React from "react";

import Header from "./Header";
import Swap from "./Swap";
import About from "./About";
import Footer from "./Footer";

import { Container, Subscribe } from "unstated";
import { configureGSN } from "@opengsn/gsn/dist/src/relayclient/GSNConfigurator";
import { RelayProvider } from "@opengsn/gsn/dist/src/relayclient"
import { Web3Provider } from "ethers/providers";
import { getHttpProvider } from "../lib/web3";
import { GSNConfigs } from "@btcswap/abi";
import GatewayJS from "@renproject/gateway";

export interface AppState {
    web3Provider: Web3Provider | null
    gsnProvider: Web3Provider | null
    gateway: GatewayJS | null
    address: string
    errorMsg: string
}

export interface AppActions {
    initGSN: () => Promise<void>;
}

export class AppContainer extends Container<AppState> {
    state: AppState = {
        web3Provider: null,
        gsnProvider: null,
        gateway: null,
        errorMsg: "",
        address: ""
    }

    initGSN: AppActions["initGSN"] = async () => {
        const httpProvider = await getHttpProvider();
        const web3Provider = new Web3Provider(httpProvider);
        const net = await web3Provider.getNetwork();
        const conf = GSNConfigs.get(net.name);

        if (!conf) {
            this.setState({ errorMsg: "unsupported network" });
            return;
        }

        const gsnConfig = configureGSN({
            relayHubAddress: conf.RelatHub,
            paymasterAddress: conf.Paymaster,
            stakeManagerAddress: conf.StakeManager,
            gasPriceFactorPercent: 70,
            methodSuffix: '_v4',
            jsonStringifyRequest: true,
            chainId: net.chainId,
            relayLookupWindowBlocks: 1e5
        })

        const relayProvider = new RelayProvider(httpProvider, gsnConfig);
        let gateway;
        switch (net.chainId) {
            case 1:
                gateway = new GatewayJS("mainnet");
                break;
            case 42:
                gateway = new GatewayJS("testnet");
                break;
        }

        const address = await web3Provider.getSigner().getAddress();
        this.setState({ gateway, web3Provider: web3Provider, gsnProvider: new Web3Provider(relayProvider), address });
    }
}

class App extends React.Component {
    render(): JSX.Element {
        return (
            <div className="app">
                <Subscribe to={[AppContainer]}>
                    {(container: AppContainer) => {
                        console.log("initing providers !!!", container.state.web3Provider, container.state.gateway);
                        return (
                            <>
                                <Header gsnProvider={container.state.gsnProvider} web3Provider={container.state.web3Provider} address={container.state.address} initGSN={container.initGSN} />
                                <Swap gsnProvider={container.state.gsnProvider} web3Provider={container.state.web3Provider} gateway={container.state.gateway} />
                                <About />
                                <Footer />
                            </>
                        )
                    }}
                </Subscribe>
            </div >
        );
    }
}

export default App;
