import React, { Component } from 'react'
import { AppState } from './App';
import { ContractType, getContracts } from "@btcswap/abi";
import GatewayJS from "@renproject/gateway";
import { Contract } from 'ethers';
import { defaultAbiCoder } from 'ethers/utils';
import { Token } from "../lib/swap"
import Swapper from './Swapper';
import Sender from './Sender';


enum Option {
    Send,
    Swap
}

interface State {
    errorMsg: string,
    option: Option
}

interface Props {
    web3Provider: AppState["web3Provider"];
    gsnProvider: AppState["gsnProvider"];
    gateway: AppState["gateway"];
}

class Swap extends Component<Props, State> {
    state: State = {
        errorMsg: "",
        option: Option.Swap,
    }

    render() {
        const { web3Provider, gsnProvider } = this.props;
        const { option, errorMsg } = this.state;
        return (
            <div className="swap">
                <div className="container">
                    <div className="tabs" onClick={this.toggleTab}>
                        <span className={`item ${option === Option.Swap ? 'active' : ''}`}>Swap</span>
                        <span className={`item ${option === Option.Send ? 'active' : ''}`}>Send</span>
                        <span className={`indicator ${option === Option.Swap ? 'left' : 'right'}`}></span>
                    </div>

                    {option === Option.Swap ?
                        <Swapper onSwap={this.swap} disabled={!web3Provider || !gsnProvider} />
                        :
                        <Sender onSend={this.swap} disabled={!web3Provider || !gsnProvider} />}
                    {errorMsg !== "" &&
                        <p className="error">{errorMsg}</p>
                    }
                </div>
            </div>
        )
    }

    toggleTab = () => {
        const { option } = this.state;
        let newOption = Option.Swap;
        if (option === Option.Swap) {
            newOption = Option.Send;
        }
        this.setState({ option: newOption });
    }

    swap = async (sendToken: Token, receiveToken: Token, amount: string, receiveBtcAddress: string) => {
        this.setState({ errorMsg: "" });
        const { web3Provider, gsnProvider } = this.props;
        if (!web3Provider || !gsnProvider) {
            this.setState({ errorMsg: "Please enable Web3 or download the MetaMask extension" });
            return;
        }
        const web3ContractMap = await getContracts(web3Provider);
        const gsnContractMap = await getContracts(gsnProvider);
        const renBTC = web3ContractMap.get(ContractType.RenBTC);
        const wBTC = web3ContractMap.get(ContractType.WBTC);
        const multiBTC = gsnContractMap.get(ContractType.MultiCollateralBitcoin);
        const renBTCAdapter = gsnContractMap.get(ContractType.RenBTCAdapter);
        if (!renBTC || !wBTC || !multiBTC || !renBTCAdapter) {
            this.setState({ errorMsg: "Failed to connect to contracts, please check your connection" });
            return;
        }

        const value = Math.floor(parseFloat(amount) * (10 ** 8));
        console.log(sendToken, receiveToken, amount, receiveBtcAddress);

        if (receiveToken === Token.BTC && receiveBtcAddress === "") {
            this.setState({ errorMsg: "Please enter a valid Bitcoin address to receive your funds" })
            return;
        }

        if (sendToken === receiveToken) {
            await this.transfer(wBTC, multiBTC, receiveBtcAddress, value, receiveToken);
            return;
        }

        if (sendToken === Token.MultiBTC) {
            await this.withdraw(renBTC, multiBTC, wBTC, receiveBtcAddress, value, receiveToken);
            return;
        }

        if (receiveToken === Token.MultiBTC) {
            await this.deposit(renBTC, multiBTC, wBTC, value, sendToken);
            return;
        }

        if (receiveToken === Token.BTC && sendToken === Token.WBTC) {
            await this.swapWBTCToBTC(renBTC, multiBTC, wBTC, renBTCAdapter, receiveBtcAddress, value);
        } else if (receiveToken === Token.WBTC && sendToken === Token.BTC) {
            await this.swapBTCToWBTC(renBTC, multiBTC, wBTC, value);
        } else {
            this.setState({ errorMsg: `Unsupported coin pair ${receiveToken} -> ${sendToken}` });
        }
    }

    swapWBTCToBTC = async (renBTC: Contract, multiBTC: Contract, wBTC: Contract, renBTCAdapter: Contract, toAddress: string, amount: number) => {
        const { web3Provider, gateway } = this.props;
        if (!web3Provider || !gateway) {
            this.setState({ errorMsg: "Please enable Web3 or download the MetaMask extension" });
            return;
        }
        const address = await web3Provider.getSigner().getAddress();

        const balance = await renBTC.balanceOf(multiBTC.address);
        if (balance < amount) {
            this.setState({ errorMsg: `Insufficent liquidity. Please use a lower amount (maximum: ${balance} BTC)` });
            return;
        }

        const allowance = await wBTC.allowance(address, renBTCAdapter.address);
        if (allowance < amount) {
            await wBTC.approve(renBTCAdapter.address, amount.toString());
        }

        await gateway.open({
            // Send BTC from the Ethereum blockchain to the Bitcoin blockchain.
            // This is the reverse of shitIn.
            sendToken: GatewayJS.Tokens.BTC.Eth2Btc,

            // The contract we want to interact with
            sendTo: renBTCAdapter.address,

            // The name of the function we want to call
            contractFn: "swapOut",

            // Arguments expected for calling `deposit`
            contractParams: [
                { name: "_fromBtcVariant", type: "address", value: wBTC.address },
                { name: "_to", type: "bytes", value: "0x" + Buffer.from(toAddress).toString("hex") },
                { name: "_amount", type: "uint256", value: Math.floor(amount) },
            ],

            // Web3 provider for submitting burn to Ethereum
            web3Provider: web3Provider,
        }).result();

        this.log(`Transferred ${amount} BTC to ${toAddress}.`);
    }

    swapBTCToWBTC = async (renBTC: Contract, multiBTC: Contract, wBTC: Contract, amount: number) => {
        const { gsnProvider, gateway } = this.props;
        if (!gsnProvider || !gateway) {
            this.setState({ errorMsg: "Please enable Web3 or download the MetaMask extension" });
            return;
        }

        const balance = await wBTC.balanceOf(multiBTC.address);
        if (balance < amount) {
            this.setState({ errorMsg: `Insufficent liquidity. Please use a lower amount (maximum: ${balance} WBTC)` });
            return;
        }

        try {
            await gateway.open({
                // Send BTC from the Bitcoin blockchain to the Ethereum blockchain.
                sendToken: GatewayJS.Tokens.BTC.Btc2Eth,

                // Amount of BTC we are sending (in Satoshis)
                suggestedAmount: Math.floor(amount), // Convert to Satoshis

                // The contract we want to interact with
                sendTo: renBTC.address,

                // The name of the function we want to call
                contractFn: "swapIn",

                // The nonce is used to guarantee a unique deposit address
                nonce: GatewayJS.utils.randomNonce(),

                // Arguments expected for calling `deposit`
                contractParams: [
                    {
                        name: "_msg",
                        type: "bytes",
                        value: defaultAbiCoder.encode(["address"], [wBTC.address]),
                    }
                ],

                // Web3 provider for submitting mint to Ethereum
                web3Provider: gsnProvider,
            }).result();
            this.log(`Deposited ${amount} BTC.`);
        } catch (error) {
            // Handle error
            this.logError(error);
        }
    }

    // recoverTransfers = async() => {
    //     const { web3Provider, gateway } = this.props;
    //     if (!gateway || !web3Provider) {
    //         this.setState({ errorMsg: "failed to initialize web3 provider" });
    //         return;
    //     }

    //     // Load previous transfers from local storage
    //     const previousGateways = await gateway.getGateways();
    //     // Resume each transfer
    //     for (const transfer of Array.from(previousGateways.values())) {
    //         gateway
    //         .recoverTransfer(web3Provider, transfer)
    //         .pause()
    //         .result()
    //         .catch(this.logError);
    //     }
    // }

    deposit = async (renBTC: Contract, multiBTC: Contract, wBTC: Contract, value: number, coinType: string) => {
        switch (coinType) {
            case "btc":
                await this.depositBtc(renBTC, value);
                break;
            case "wbtc":
                await this.depositWBTC(multiBTC, wBTC, value);
                break;
            default:
                this.setState({ errorMsg: `unsupported coin type ${coinType}` });
                break;
        }
    }

    depositWBTC = async (multiBTC: Contract, wBTC: Contract, amount: number) => {
        const { web3Provider } = this.props;
        if (!web3Provider) {
            this.setState({ errorMsg: "Please enable Web3 or download the MetaMask extension" });
            return;
        }
        const address = await web3Provider.getSigner().getAddress();

        const allowance = await wBTC.allowance(address, multiBTC.address);

        if (allowance < amount) {
            await wBTC.approve(multiBTC.address, amount.toString());
        }
        await multiBTC.deposit(wBTC.address, amount.toString());
    }

    depositBtc = async (renBTC: Contract, amount: number) => {
        const { web3Provider, gateway } = this.props;
        if (!web3Provider || !gateway) {
            this.setState({ errorMsg: "Please enable Web3 or download the MetaMask extension" });
            return;
        }

        if (!renBTC) {
            this.setState({ errorMsg: "Failed to connect to contracts, please check your connection" });
            return;
        }

        try {
            await gateway.open({
                // Send BTC from the Bitcoin blockchain to the Ethereum blockchain.
                sendToken: GatewayJS.Tokens.BTC.Btc2Eth,

                // Amount of BTC we are sending (in Satoshis)
                suggestedAmount: amount, // Convert to Satoshis

                // The contract we want to interact with
                sendTo: renBTC.address,

                // The name of the function we want to call
                contractFn: "deposit",

                // The nonce is used to guarantee a unique deposit address
                nonce: GatewayJS.utils.randomNonce(),

                // Arguments expected for calling `deposit`
                contractParams: [
                    {
                        name: "_msg",
                        type: "bytes",
                        value: "0x" + new Buffer(`Convert ${amount} BTC to MultiBTC`).toString("hex"),
                    }
                ],

                // Web3 provider for submitting mint to Ethereum
                web3Provider: web3Provider,
            }).result();
            this.log(`Deposited ${amount} BTC.`);
        } catch (error) {
            // Handle error
            this.logError(error);
        }
    }

    withdraw = async (renBTC: Contract, multiBTC: Contract, wBTC: Contract, toBtcAddress: string, value: number, coinType: string) => {
        switch (coinType) {
            case "btc":
                await this.withdrawBTC(renBTC, multiBTC, toBtcAddress, value);
                break;
            case "wbtc":
                await this.withdrawWBTC(multiBTC, wBTC, value);
                break;
            default:
                this.setState({ errorMsg: `Unsupported coin type ${coinType}` });
                break;
        }
    }

    withdrawBTC = async (renBTC: Contract, multiBTC: Contract, toBtcAddress: string, amount: number) => {
        const { web3Provider, gateway } = this.props;
        if (!web3Provider || !gateway) {
            this.setState({ errorMsg: "Please enable Web3 or download the MetaMask extension" });
            return;
        }
        const address = await web3Provider.getSigner().getAddress();

        const allowance = await multiBTC.allowance(address, renBTC.address);
        if (allowance < amount) {
            await multiBTC.approve(renBTC.address, amount.toString());
        }

        // You can surround shiftOut with a try/catch to handle errors.
        await gateway.open({
            // Send BTC from the Ethereum blockchain to the Bitcoin blockchain.
            // This is the reverse of shitIn.
            sendToken: GatewayJS.Tokens.BTC.Eth2Btc,

            // The contract we want to interact with
            sendTo: renBTC.address,

            // The name of the function we want to call
            contractFn: "withdraw",

            // Arguments expected for calling `deposit`
            contractParams: [
                { name: "_to", type: "bytes", value: "0x" + Buffer.from(toBtcAddress).toString("hex") },
                { name: "_amount", type: "uint256", value: Math.floor(amount) },
            ],

            // Web3 provider for submitting burn to Ethereum
            web3Provider: web3Provider,
        }).result();

        this.log(`Withdrew ${amount} BTC to ${toBtcAddress}.`);
    }

    withdrawWBTC = async (multiBTC: Contract, wBTC: Contract, amount: number) => {
        await multiBTC.withdraw(wBTC.address, amount.toString());
    }

    transfer = async (wBTC: Contract, multiBTC: Contract, address: string, amount: number, coinType: string) => {
        switch (coinType) {
            case "wbtc":
                await wBTC.transfer(address, amount.toString());
                break;
            case "multibtc":
                await multiBTC.transfer(address, amount.toString());
                break;
            default:
                this.setState({ errorMsg: `Unsupported coin type ${coinType}` });
                break;
        }
    }

    log = async (message: string) => {
        console.log(message);
    }

    logError = async (message: string) => {
        console.error(message);
    }
}

export default Swap