import React from 'react'
import { AppActions, AppState } from "./App";
import { Web3Provider } from 'ethers/providers';
import CopyToClipboard from 'react-copy-to-clipboard';

import { ReactComponent as Logo } from "../styles/images/logo.svg";

interface Props {
    initGSN: AppActions["initGSN"];
    address: AppState["address"];
    gsnProvider: AppState["gsnProvider"];
    web3Provider: AppState["web3Provider"];
}

interface State {
    copied: boolean
    buttonMsg: string
}

class Header extends React.Component<Props, State> {
    constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
            copied: false,
            buttonMsg: ""
        };
    }

    onCopy = () => {
        this.setState({ buttonMsg: "Copied!" });
        setTimeout(() => {
            this.setState({ buttonMsg: "" });
        }, 1000);
    };

    componentDidMount() {
        const { initGSN } = this.props;
        initGSN();
    }

    async getAddressTag(web3Provider: Web3Provider): Promise<string> {
        return web3Provider.getSigner().getAddress();
    }

    render(): JSX.Element {
        const { address, gsnProvider, web3Provider } = this.props;
        const { buttonMsg } = this.state;

        return (
            <div className="header">
                {!gsnProvider && !web3Provider &&
                    <p className="warning">Please connect to Mainnet or Kovan on your dapp browser</p>
                }
                <div className="container">
                    <Logo className="logo" />
                    <CopyToClipboard
                        onCopy={this.onCopy}
                        text={address}>
                        <button>
                            {web3Provider ?
                                buttonMsg === "" ? `${address.substr(0, 6)}...${address.slice(-4)}` : buttonMsg
                                :
                                "Connect"
                            }
                        </button>
                    </CopyToClipboard>
                </div>
            </div>
        );
    }
}

export default Header;
