import React from 'react'
import { SelectTag } from './SelectTag'
import { Token } from "../lib/swap"

interface Props {
    disabled: boolean
    onSend: (sendToken: Token, receiveToken: Token, amount: string, receiveBtcAddress: string) => void
}

interface State {
    token: Token,
    receiveBtcAddress: string,
    amount: string,
}

class Sender extends React.Component<Props, State> {
    constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
            token: Token.MultiBTC,
            receiveBtcAddress: "",
            amount: "",
        };
    }

    changeToken = (token: Token) => {
        this.setState({ token });
    }

    handleAmount = (event: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({ amount: event.target.value });
    }

    handleAddress = (event: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({ receiveBtcAddress: event.target.value });
    }

    setSwapDetails = () => {
        const { onSend } = this.props;
        const { token, amount, receiveBtcAddress } = this.state;
        let receiveToken: Token;
        if (!this.isEthAddress(receiveBtcAddress)) {
            receiveToken = Token.BTC;
        } else {
            receiveToken = token
        }
        onSend(token, receiveToken, amount, receiveBtcAddress);
    }

    isEthAddress(address: string) {
        return (address.length === 42 || address.length === 40);
    }

    render(): JSX.Element {
        const { token } = this.state;
        const { disabled } = this.props;

        return (
            <>
                <div className="bg">
                    <input className="input" type="text" placeholder={"0.0"} onChange={this.handleAmount} disabled={disabled} />
                    <SelectTag changeValue={this.changeToken} defaultOption={token} removeToken={Token.BTC} disabled={disabled} />
                </div>
                <div className="bg full">
                    <input className="input" type="text" placeholder={"Enter recipient address"} onChange={this.handleAddress} disabled={disabled} />
                </div>
                <button className="button" disabled={disabled} onClick={this.setSwapDetails}>Send</button>
            </>
        );
    }
}

export default Sender;
